/* Slider */

	.ls-container,
	.ls-wp-container {
		visibility: hidden;
		position: relative;
		max-width: 100%;
	}

	.ls-inner {
		position: relative;
		width: 100%;
		height: 100%;
		background-position: center center;
		z-index: 30;
	}

	.ls-wp-fullwidth-container {
		width: 100%;
		position: relative;
	}

	.ls-wp-fullwidth-helper {
		position: absolute;
	}



/* Wrappers */

	ls-scene-wrapper {
		width: 100%;
		margin: 0 auto;
		display: block !important;
		position: relative !important;
	}

	ls-scene-wrapper .ls-container {
		position: sticky !important;
		top: 0;
	}

	ls-silent-wrapper {
		width: 100vw;
		height: 100vh;
		z-index: -1 !important;
		visibility: hidden !important;
		opacity: 0 !important;
		pointer-events: none !important;
		position: fixed !important;
		left:  -200vw;
		top: -200vh;
	}

	ls-scene-wrapper[data-disabled-scene] {
		height: auto !important;
	}



/* Layers */

	.ls-slide-bgcolor,
	.ls-slide-backgrounds,
	.ls-slide-effects,
	.ls-background-videos,
	.ls-bg-video-overlay {
		-o-transform: translateZ(0);
		-ms-transform: translateZ(0);
		-moz-transform: translateZ(0);
		-webkit-transform: translateZ(0);
		transform: translateZ(0);
	}

	.ls-layers,
	.ls-slide-bgcolor,
	.ls-slide-backgrounds,
	.ls-slide-effects,
	.ls-background-videos,
	.ls-bg-video-overlay {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		overflow: hidden;
	}

	.ls-text-layer .char .ls-grad-bg-overflow {
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: -1;
	}

	.ls-wrapper {
		padding: 0 !important;
		visibility: visible !important;
		border: 0 !important;
		z-index: 1;
	}

	.ls-wrapper.ls-fix-textnodes .line,
	.ls-wrapper.ls-fix-textnodes .word {
		position: static !important;
		transform: none !important;
	}

	.ls-wrapper .line {
		white-space: nowrap;
	}

	.ls-layer.ls-has-chars .word {
		position: static !important;
	}

	.ls-layer.ls-has-chars .char {
		position: relative !important;
	}

	.ls-layer.ls-has-words .word {
		position: relative !important;
	}

	.ls-layer.ls-has-lines .line {
		position: relative !important;
	}

	.ls-layer .char,
	.ls-layer .word,
	.ls-layer .line {
		text-decoration: inherit;
	}

	.ls-layer[data-ls-text-animating-in][data-ls-mask-text-in] .char,
	.ls-layer[data-ls-text-animating-in][data-ls-mask-text-in] .word,
	.ls-layer[data-ls-text-animating-in][data-ls-mask-text-in] .line,
	.ls-layer[data-ls-text-animating-out][data-ls-mask-text-out] .char,
	.ls-layer[data-ls-text-animating-out][data-ls-mask-text-out] .word,
	.ls-layer[data-ls-text-animating-out][data-ls-mask-text-out] .line {
		clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
	}

	.ls-textnode-dummy {
		display: inline-block !important;
		visibility: hidden !important;
		pointer-events: none !important;
	}

	.ls-textnode-bgclip-wrap {
		left: 0;
		top: 0;
		position: absolute;
		background-clip: text;
		-webkit-background-clip: text;
		text-fill-color: transparent;
		-webkit-text-fill-color: transparent;
		text-align: left !important;
		pointer-events: none !important;
	}

	.ls-textnode {
		color: transparent;
		text-fill-color: transparent;
		-webkit-text-fill-color: transparent;
	}

	.ls-wrapper.ls-force-display-block {
		display: block !important;
	}

	.ls-wrapper.ls-force-visibility-hidden {
		visibility: hidden !important;
	}

	.ls-wrapper.ls-force-left-0 {
		left: 0 !important;
	}

	.ls-wrapper.ls-force-width-auto {
		width: auto !important;
	}

	.ls-force-height-auto {
		height: auto !important;
	}

	.ls-wrapper.ls-force-full-size {
		width: 100% !important;
		height: 100% !important;
	}

	.ls-layer.ls-force-no-transform,
	.ls-wrapper.ls-force-no-transform {
		transform: none !important;
	}

	.ls-force-no-transform .line,
	.ls-force-no-transform .word,
	.ls-force-no-transform .char {
		transform: none !important;
	}

	.ls-background-videos {
		z-index: 1;
	}

	.ls-bg-video-overlay {
		z-index: 21;
		background-repeat: repeat;
		background-position: center center;
	}

	.ls-slide-backgrounds {
		z-index: 2;
	}

	.ls-slide-effects {
		z-index: 4;
	}

	.ls-slide-bgcolor {
		z-index: 1;
	}

	.ls-layers {
		z-index: 35;
	}

	.ls-z,
	.ls-parallax,
	.ls-in-out,
	.ls-scroll,
	.ls-scroll-transform,
	.ls-loop,
	.ls-clip {
		position: absolute !important;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}

	.ls-clip,
	.ls-m-layers-clip {
		clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
	}

	.ls-layers-clip {
		clip: rect( 0px, var( --sw ), var( --sh ), 0px );
	}

	.ls-z {
		width: 100% !important;
		height: 100% !important;
		top: 0 !important;
		left: 0 !important;
		bottom: auto !important;
		right: auto !important;
		display: block !important;
		overflow: visible !important;
		pointer-events: none !important;
	}

	.ls-z.ls-wrapper.ls-pinned {
		position: fixed !important;
	}

	.ls-slide-backgrounds .ls-wrapper.ls-z {
		overflow: hidden !important;
	}

	.ls-inner-wrapper {
		pointer-events: auto;
	}

	.ls-inner-wrapper * {
		pointer-events: inherit;
	}

	.ls-in-out {
		display: none;
	}

	.ls-loop, .ls-clip {
		display: block !important;
	}

	.ls-parallax,
	.ls-in-out,
	.ls-scroll,
	.ls-scroll-transform,
	.ls-loop,
	.ls-z {
		background: none !important;
	}

	.ls-bg-wrap,
	.ls-loop {
		margin: 0 !important;
	}

	.ls-wrapper > .ls-layer,
	.ls-wrapper > a > .ls-layer
	.ls-wrapper > .ls-bg {
		line-height: normal;
		white-space: nowrap;
		word-wrap: normal;
		margin-right: 0 !important;
		margin-bottom: 0 !important;
	}

	.ls-wrapper > .ls-layer,
	.ls-wrapper > a > .ls-layer {
		position: static !important;
		display: block !important;
		transform: translateZ(0);
	}

	.ls-bg-wrap,
	.ls-bg-outer {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}

	.ls-bg-wrap {
		display: none;
	}

	.ls-bg-outer {
		overflow: hidden !important;
	}

	.ls-wrapper > a.ls-layer-link {
		cursor: pointer !important;
		display: block !important;
		position: absolute !important;
		left: 0 !important;
		top: 0 !important;
		width: 100% !important;
		height: 100% !important;
		z-index: 10;
		-o-transform: translateZ(100px);
		-ms-transform: translateZ(100px);
		-moz-transform: translateZ(100px);
		-webkit-transform: translateZ(100px);
		transform: translateZ(100px);
		background: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7) !important;
	}



/* GUI */



	/* Common */

	.ls-gui-element {
		z-index: 40;
		text-decoration: none !important;
	}

	.ls-loading-container {
		position: absolute !important;
		display: none;
		left: 50% !important;
		top: 50% !important;
	}

	.ls-loading-indicator {
		margin: 0 auto;
	}



	/* Media */

	.ls-media-unmute {
		visibility: hidden;
		position: absolute;
		left: 16px;
		top: 16px;
		width: 30px;
		height: 30px;
		cursor: pointer;
		opacity: .65;
	}

	.ls-media-unmute-active {
		visibility: visible;
	}

	.ls-media-unmute-bg {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		border-radius: 30px;
		border: 0 solid white;
		transform: scale(0.1);
		background: rgba(0,0,0,.25);
		box-shadow: 0 3px 10px rgba(0,0,0,.2);
	}

	.ls-icon-muted, .ls-icon-unmuted {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-position: center;
		background-size: 20px;
		transform: scale(0.1);
	}

	.ls-media-unmute-active .ls-media-unmute-bg {
		border: 2px solid white;
	}

	.ls-media-unmute-active .ls-icon-muted,
	.ls-media-unmute-active .ls-icon-unmuted,
	.ls-media-unmute-active .ls-media-unmute-bg {
		transform: scale(1) !important;
	}

	.ls-icon-muted {
		background-image: url(../images/icon-muted-white.png);
		opacity: 1;
	}

	.ls-icon-unmuted {
		background-image: url(../images/icon-unmuted-white.png);
		opacity: 0;
	}

	.ls-media-unmute:hover {
		opacity: 1;
	}

	.ls-media-unmute:hover .ls-icon-muted {
		opacity: 0;
	}

	.ls-media-unmute:hover .ls-icon-unmuted {
		opacity: 1;
	}

	.ls-media-unmute-bg,
	.ls-icon-unmuted,
	.ls-media-unmute,
	.ls-icon-muted {
		transition: all .3s ease-in-out !important;
	}



	/* Navigation */

		.ls-bottom-slidebuttons {
			text-align: left;
		}

		.ls-bottom-nav-wrapper,
		.ls-below-thumbnails {
			height: 0;
			position: relative;
			text-align: center;
			margin: 0 auto;
		}

		.ls-below-thumbnails {
			display: none;
		}

		.ls-bottom-nav-wrapper a,
		.ls-nav-prev,
		.ls-nav-next {
			outline: none;
		}

		.ls-bottom-slidebuttons {
			position: relative;
		}

		.ls-bottom-slidebuttons,
		.ls-nav-start,
		.ls-nav-stop,
		.ls-nav-sides {
			position: relative;
		}

		.ls-nothumb {
			text-align: center !important;
		}

		.ls-link,
		.ls-link:hover {
			position: absolute !important;
			width: 100% !important;
			height: 100% !important;
			left: 0 !important;
			top: 0 !important;
		}

		.ls-link {
			display: none;
		}

		.ls-link-on-top {
			z-index: 999999 !important;
		}

		.ls-link, .ls-wrapper > a > * {
			background-image: url('data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7');
		}

		.ls-bottom-nav-wrapper,
		.ls-thumbnail-wrapper,
		.ls-nav-prev,
		.ls-nav-next {
			visibility: hidden;
		}



	/* Timers */

		.ls-bar-timer {
			position: absolute;
			width: 0;
			height: 2px;
			background: white;
			border-bottom: 2px solid #555;
			opacity: .55;
			top: 0;
		}

		.ls-circle-timer {
			width: 16px;
			height: 16px;
			position: absolute;
			right: 10px;
			top: 10px;
			opacity: .65;
			display: none;
		}

		.ls-container .ls-circle-timer,
		.ls-container .ls-circle-timer * {
			box-sizing: border-box !important;
		}

		.ls-ct-half {
			border: 4px solid white;
		}

		.ls-ct-left,
		.ls-ct-right {
			width: 50%;
			height: 100%;
			overflow: hidden;
		}

		.ls-ct-left,
		.ls-ct-right {
			position: absolute;
		}

		.ls-ct-left {
			left: 0;
		}

		.ls-ct-right {
			left: 50%;
		}

		.ls-ct-rotate {
			width: 200%;
			height: 100%;
			position: absolute;
			top: 0;
		}

		.ls-ct-left .ls-ct-rotate,
		.ls-ct-right .ls-ct-hider,
		.ls-ct-right .ls-ct-half {
			left: 0;
		}

		.ls-ct-right .ls-ct-rotate,
		.ls-ct-left .ls-ct-hider,
		.ls-ct-left .ls-ct-half {
			right: 0;
		}

		.ls-ct-hider,
		.ls-ct-half {
			position: absolute;
			top: 0;
		}

		.ls-ct-hider {
			width: 50%;
			height: 100%;
			overflow: hidden;
		}

		.ls-ct-half {
			width: 200%;
			height: 100%;
		}

		.ls-ct-center {
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			position: absolute;
			border: 4px solid black;
			opacity: 0.2;
		}

		.ls-ct-half,
		.ls-ct-center {
			border-radius: 100px;
			-moz-border-radius: 100px;
			-webkit-border-radius: 100px;
		}

		.ls-slidebar-container {
			margin: 10px auto;
		}

		.ls-slidebar-container,
		.ls-slidebar {
			position: relative;
		}

		.ls-progressbar,
		.ls-slidebar-slider-container {
			position: absolute;
			left: 0;
			top: 0;
		}

		.ls-progressbar {
			width: 0;
			height: 100%;
			z-index: 1;
		}

		.ls-slidebar-slider-container {
			z-index: 2;
		}

		.ls-slidebar-slider {
			position: static !important;
			cursor: pointer;
		}

		/* slidebar default dark skin */

		.ls-slidebar {
			height: 10px;
			background-color: #ddd;
		}

		.ls-slidebar,
		.ls-progressbar {
			border-radius: 10px;
		}

		.ls-progressbar {
			background-color: #bbb;
		}

		.ls-slidebar-slider-container {
			top: 50%;
		}

		.ls-slidebar-slider {
			width: 20px;
			height: 20px;
			border-radius: 20px;
			background-color: #999;
		}

		/* slidebar default light skin */

		.ls-slidebar-light .ls-slidebar {
			height: 8px;
			background-color: white;
			border: 1px solid #ddd;
			border-top: 1px solid #ccc;
		}

		.ls-slidebar-light .ls-slidebar,
		.ls-slidebar-light .ls-progressbar {
			border-radius: 10px;
		}

		.ls-slidebar-light .ls-progressbar {
			background-color: #eee;
		}

		.ls-slidebar-light .ls-slidebar-slider-container {
			top: 50%;
		}

		.ls-slidebar-light .ls-slidebar-slider {
			width: 18px;
			height: 18px;
			border-radius: 20px;
			background-color: #eee;
			border: 1px solid #bbb;
			border-top: 1px solid #ccc;
			border-bottom: 1px solid #999;
		}




	/* Thumbnails */

		.ls-tn {
			display: none !important;
		}

		.ls-thumbnail-hover {
			display: none;
			position: absolute;
			left: 0;
		}

		.ls-thumbnail-hover-inner {
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
			display: none;
		}

		.ls-thumbnail-hover-bg {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
		}

		.ls-thumbnail-hover-img {
			position: absolute;
			overflow: hidden;
		}

		.ls-thumbnail-hover img {
			max-width: none !important;
			position: absolute;
			display: inline-block;
			visibility: visible !important;
			left: 50%;
			top: 0;
		}

		.ls-thumbnail-hover span {
			left: 50%;
			top: 100%;
			width: 0;
			height: 0;
			display: block;
			position: absolute;
			border-left-color: transparent !important;
			border-right-color: transparent !important;
			border-bottom-color: transparent !important;
		}

		.ls-thumbnail-wrapper {
			position: relative;
			width: 100%;
			margin: 0 auto;
		}

		.ls-thumbnail-wrapper.ls-thumbnail-fill  a img {
			width: 100%;
		}

		.ls-thumbnail {
			position: relative;
			margin: 0 auto;
		}

		.ls-thumbnail-inner,
		.ls-thumbnail-slide-container {
			width: 100%;
		}

		.ls-thumbnail-slide-container {
			overflow: hidden !important;
			position: relative;
		}

		.ls-thumbnail-slide {
			text-align: center;
			white-space: nowrap;
			float: left;
			position: relative;
		}

		.ls-thumbnail-slide a {
			overflow: hidden;
			display: inline-block;
			width: 0;
			height: 0;
			position: relative;
		}

		.ls-thumbnail-slide img {
			max-width: none !important;
			max-height: 100% !important;
			height: 100%;
			visibility: visible !important;
			background-position: center !important;
		}

		.ls-touchscroll {
			overflow-x: auto !important;
		}



	/* Shadow */

		.ls-shadow {
			display: none;
			position: absolute;
			z-index: 10 !important;
			top: 100%;
			width: 100%;
			left: 0;
			overflow: hidden !important;
			visibility: hidden;
		}

		.ls-shadow img {
			width: 100% !important;
			height: auto !important;
			position: absolute !important;
			left: 0 !important;
			bottom: 0 !important;
		}



/* Media */

	.ls-media-layer {
		overflow: hidden;
	}

	.ls-vpcontainer,
	.ls-videopreview {
		width : 100%;
		height : 100%;
		position : absolute;
		left : 0;
		top : 0;
		cursor : pointer;
	}

	.ls-videopreview {
		background-size: cover;
		background-position: center center;
	}

	.ls-videopreview {
		z-index: 1;
	}

	.ls-playvideo {
		position: absolute;
		left: 50%;
		top: 50%;
		cursor: pointer;
		z-index: 2;
	}

	.ls-autoplay .ls-playvideo,
	.ls-instant-autoplay .ls-playvideo {
		display: none !important;
	}



/* Mobile features */

	.ls-forcehide {
		display: none !important;
	}



/* YourLogo */

	.ls-yourlogo {
		position: absolute;
		z-index: 50;
	}



/* Slide transitions */

	/* .ls-lt-container */
	.ls-slide-transition-wrapper {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 3;
	}

	.ls-slide-transition-wrapper,
	.ls-slide-transition-wrapper * {
		text-align: left !important;
	}

	.ls-slide-transition-tile {
		position: relative;
		float: left;
	}

	.ls-slide-transition-tile img {
		visibility: visible;
		margin: 0 !important;
	}

	.ls-curtiles {
		overflow: hidden;
	}

	.ls-curtiles,
	.ls-nexttiles {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
	}

	.ls-curtile, .ls-nexttile {
		overflow: hidden;
		width: 100% !important;
		height: 100% !important;
		-o-transform: translateZ(0);
		-ms-transform: translateZ(0);
		-moz-transform: translateZ(0);
		-webkit-transform: translateZ(0);
		transform: translateZ(0);
	}

	.ls-nexttile {
		display: none;
	}

	.ls-3d-container {
		position: relative;
		overflow: visible !important;
		-ms-perspective: none !important;
		-moz-perspective: none !important;
		-webkit-perspective: none !important;
		perspective: none !important;
	}

	.ls-3d-box {
		position: absolute;
	}

	.ls-3d-box > div {
		overflow: hidden;
		margin: 0;
		padding: 0;
		position: absolute;
		background-color: #888;
	}

	.ls-3d-box .ls-3d-top,
	.ls-3d-box .ls-3d-bottom {
		background-color: #555;
	}

	.ls-slide-transition-wrapper,
	.ls-slide-transition-tile {
		-ms-perspective: 1500px;
		-moz-perspective: 1500px;
		-webkit-perspective: 1500px;
		perspective: 1500px;
	}

	.ls-slide-transition-tile,
	.ls-3d-box,
	.ls-curtiles,
	.ls-nexttiles {
		-ms-transform-style: preserve-3d;
		-moz-transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;
		transform-style: preserve-3d;
	}

	.ls-curtile,
	.ls-nexttile,
	.ls-3d-box {
		-ms-backface-visibility: hidden;
		-moz-backface-visibility: hidden;
		-webkit-backface-visibility: hidden;
